
import { mapMutations } from "vuex";
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Scroll from "@/components/scrollAll";
import NoData from "@/components/noData";
export default {
data() {
return {
clientList: [],
params: {
customerName: "",
beginDate: "",
endDate: "",
page: 1,
count: 10
},
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
isSort: false
};
},
methods: {
...mapMutations(["setIsMyClient"]),
getClient(id) {
let _this = this;
this.$vux.confirm.show({
content: "确定领取该客户？",
onConfirm() {
let user = JSON.parse(localStorage.getItem("user")),
userID = user ? user.data.UserID : 204;
_this
.postAxios(
`/Customer/SetCustomerOwner?SaleChief=${userID}&type=1`,
[id]
)
.then(res => {
_this.$vux.toast.show({
type: "success",
text: "领取成功"
});
_this.getData(1, 1);
});
}
});
},
getData(type, page) {
this.$vux.loading.show({
text: "加载中"
});

this.params.page = page || this.params.page;

this.getAxios("/Customer/GetCompCustomerList?isOwner=1", this.params)
.then(res => {
this.$vux.loading.hide();
if (res.Data.List.length > 0) {
if (type === 2) {
this.clientList = [...this.clientList, ...res.Data.List];
this.$refs.scroll.scroll.finishPullUp();
} else {
this.clientList = res.Data.List;
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.scrollTo(0, 0);
this.$refs.scroll.forceUpdate(true);
}
this.params.page += 1;
this.$refs.scroll.refresh();
} else if (type == 1) {
this.clientList = [];
this.$refs.scroll.forceUpdate();
} else {
this.$refs.scroll.forceUpdate();
}
})
.catch(err => {
this.$vux.loading.hide();
console.log(err);
});

},
goto(path, id) {
this.$router.push({
path,
query: {
id,
isCom: true
}
});
}
},
mounted() {
this.setIsMyClient(false);
this.getData();
this.$bus.on("init", () => {
this.getData();
});
},
beforeRouteLeave(to, from, next) {
if (to.path == "/") {
this.setIsMyClient(true);
}
next();
},
components: {
Flexbox,
FlexboxItem,
XHeader,
Scroll,
Group,
XInput,
NoData,
XButton
}
};



